import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { Constant } from './constant'
import { DynamicLoaderService } from './dynamic-loader.service';

@Component({
  selector: 'dynamic-loader',
  templateUrl: './dynamic-loader.component.html',
  styleUrls: ['./dynamic-loader.component.css']
})
export class DynamicLoaderComponent implements OnInit, OnDestroy {

  /**
   * To set Loader Type
   */
  @Input('loaderType') loaderType: string;
  /**
   * To toggle full screen mode
   */
  @Input('isFullScreen') isFullScreen: Boolean;
  /**
   * To set top of loader
   */
  @Input('top') top: string;
  /**
   * To set left of loader
   */
  @Input('left') left: string;
  /**
 * To set right of loader
 */
  @Input('right') right: string;
  /**
 * To set bottom of loader
 */
  @Input('bottom') bottom: string;
  /**
   * Set constant
   */
  constant = Constant;
  /**
   * To show or hide loader
   */
  isShow = false;

  constructor(private loaderService: DynamicLoaderService) { }
  /**
   * Initialization Method
   */
  ngOnInit(): void {
    console.log(this.isFullScreen);
    this.loaderService.getLoaderData().subscribe(loaderData => {
      if (loaderData) {
        loaderData.show ? this.isShow = true : this.isShow = false;  // Toggle loader
      }
    })
    if (!this.loaderType) {
      this.loaderType = this.constant.LoaderType.circularEffect  // Set Loader Type
    }
    if (this.isFullScreen == undefined) {
      this.isFullScreen = true
    }
  }

  ngOnDestroy() {
    this.isFullScreen = undefined;
  }

}
